var client = (function() {
    var engine = {};

    var browser = {};

    var system = {
        win: false,
        max: false,
        x11: false,
        ios: false,
        android: false,

        iphone: false,
        ipod: false,
        ipad: false,
        nokiaN: false,
        winMobile: false,

        wii: false,
        ps: false
    };

    var ua = navigator.userAgent;
    if (window.opera) {
        engine.ver = browser.ver = window.opera.version();
        engine.type = browser.type = "Opera";
    } else if (/AppleWebKit\/(\S+)/.test(ua)) {
        engine.ver = RegExp['$1'];
        engine.type = "webkit";

        // 确定是 Chrome 还是 Safari 还是 Edge
        if (/Edge\/(\S+)/.test(ua)) {
            engine.ver = browser.ver = 'Edge';
            engine.type = browser.type = 'Edge';
        } else if (/Chrome\/(\S+)/.test(ua)) {
            browser.ver = RegExp['$1'];
            browser.type = "Chorme";
        } else if (/Version\/(\S+)/.test(ua)) {
            browser.ver = RegExp['$1'];
            browser.type = "Safari";
        } else {
            // 近似地确定版本号
            var safariVersion = 1;
            if (engine.webkit < 100) {
                safariVersion = 1;
            } else if (engine.webkit < 312) {
                safariVersion = 1.2;
            } else if (engine.webkit < 412) {
                safariVersion = 1.3;
            } else {
                safariVersion = 2;
            }

            browser.ver = safariVersion;
        }
    } else if (/KHTML\/(\S+)/.test(ua) || /Konqueror\/(\S+)/.test(ua)) {
        engine.ver = browser.ver = RegExp['$1'];
        engine.type = browser.type = "Khtml";
    } else if (/rv:(\S+)+\) Gecko\/\d{8}/.test(ua)) {
        engine.ver = RegExp['$1'];
        engine.type = "gecko";

        // 确定是不是 Firefox
        if (/Firefox\/(\S+)/.test(ua)) {
            browser.ver = RegExp['$1'];
            browser.type = "Firefox";
        }
    } else if (/MSIE ([^;]+)/.test(ua)) {
        engine.ver = browser.ver = RegExp['$1'];
        engine.type = browser.type = "IE";
    } else if (/rv:(\S+)+\) like Gecko/) {
        engine.ver = browser.ver = RegExp['$1'];
        engine.type = browser.type = "IE";
    }

    // 检测平台
    var p = navigator.platform;
    system.win = p.indexOf('Win') == 0;
    system.mac = p.indexOf('Mac') == 0;
    system.x11 = (p == 'X11') || (p.indexOf('Linux') == 0);

    // 检测 windows 操作系统
    if (system.win) {
        if (/Win(?:dows )?([^do]{2})\s?(\d+\.\d+)?/.test(ua)) {
            if (RegExp['$1'] == 'NT') {
                switch (RegExp['$2']) {
                    case '5.0':
                        system.win = '2000';
                        break;
                    case '5.1':
                        system.win = 'XP';
                        break;
                    case '6.0':
                        system.win = 'Vista';
                        break;
                    case '6.1':
                        system.win = '7';
                        break;
                    case '6.2':
                        system.win = '8';
                        break;
                    case '10.0':
                        system.win = '10';
                        break;
                    default:
                        system.win = 'NT';
                        break;
                }
            } else if (RegExp['$1'] == '9x') {
                system.win = 'ME';
            } else {
                system.win = RegExp['$1'];
            }
        }
    }

    // 检测 macOS 版本号
    if (system.mac && ua.indexOf('Mobile') == -1) {
        if (/Mac (?:OS )?X (\d+_\d+_\d+)/.test(ua)) {
            system.mac = RegExp.$1.replace(/\_/g, '.');
        } else {
            system.mac = true; // 在无法获取版本号的情况下至少确定平台
        }
    }

    // 移动设备
    system.iphone = ua.indexOf('iPhone') > -1;
    system.ipad = ua.indexOf('iPad') > -1;
    system.ipod = ua.indexOf('iPod') > -1;
    system.nokiaN = ua.indexOf('NokiaN') > -1;

    // windows mobile
    if (system.win == 'CE') {
        system.winMobile = system.win;
    } else if (system.win == 'Ph') {
        if (/Windows Phone OS (\d+.\d+)/.test(ua)) {
            system.win = 'Phone';
            system.winMobile = parseFloat(RegExp['$1']);
        }
    }

    // 检测 iOS 版本
    if (system.mac && ua.indexOf('Mobile') > -1) {
        if (/CPU (?:iPhone )?OS (\d+_\d+)/.test(ua)) {
            system.ios = parseFloat(RegExp.$1.replace('_', '.'));
            system.mac = false;
        } else {
            system.ios = true; // 在无法获取版本号的情况下至少确定平台
            system.mac = false;
        }
    }

    // 检测安卓版本
    if (/Android (\d+\.\d+)/.test(ua)) {
        system.android = parseFloat(RegExp.$1);
    }

    // 游戏系统
    system.wii = ua.indexOf('Wii') > -1;
    system.ps = /playstation/i.test(ua);

    var temp = {};
    for (var prop in system) {
        if (!!system[prop]) {
            temp.type = prop;
            if (typeof system[prop] == "string" || typeof system[prop] == "number") {
                temp.ver = system[prop];
            } else {
                temp.ver = "can't define the version the system";
            }
        }
    }

    // return
    return {
        engine: engine,
        browser: browser,
        system: temp
    };
})();
